import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MinimaApiService } from '../service/minima-api.service';
import { IonTextarea, IonSlide, IonRange, IonInput } from '@ionic/angular';

@Component({
  selector: 'app-create-token',
  templateUrl: './create-token.page.html',
  styleUrls: ['./create-token.page.scss'],
})
export class CreateTokenPage implements OnInit {

  @ViewChild('nameTextArea', {static: false}) nameText: IonTextarea;
  @ViewChild('tokenSlider', {static: false}) tokenSlider: IonRange;
  @ViewChild('amountRef', {static:false}) amountRef: IonInput;

  forNewToken = 0;
  tokenAmount = 0;
  tokenName = '?';
  balance = 0;
  

  constructor(private api: MinimaApiService) {}

  ionViewDidEnter(){

    this.api.getBalance().then((res : any) => {
      console.log(res);
      
      let conf = res.response.confirmed;
     
      for(let i=0; i < conf.length; i++) {
        this.balance = Math.round(conf[i].amount * 100) / 100;
      }

    
      
      //this.balance = Math.round(res.response.confirmed.amount * 100) / 100;
      console.log('Balance set');
    });
  }
  ngOnInit() {
    
  }

  onAmountChanged(event: any) {
    this.tokenAmount = event.target.value;

    let regExp = new RegExp('^[a-zA-Z]+$');
    let regExpSpecial = new RegExp('[^\w\d]');

    if (regExp.test(this.tokenAmount.toString())) {
      event.target.value = this.tokenAmount.toString().slice(0, -1);
    }
  }

  resetForm() {
    this.nameText.value = '';
    //this.tokenSlider.value = 0;
    this.amountRef.value = '';
    this.forNewToken = 0;
    this.tokenAmount = 0;
    this.tokenName = '';

  }
  
  onUpdateTokenName(event: any){
    this.tokenName = event.target.value;
  }

  alertUser() {
    alert("Fill out form below please");
  }
}
